.nav {
  grid-area: n;
  font-size: 20px;
  text-align: center;
  line-height: 60px;
  max-height: 90vh;
  border-radius: 10px 10px 10px 10px;
  margin: 15px 15px 0 0;
  background-color: rgba(221, 218, 218, 0.474);
  box-shadow: 0px 0px 3px rgb(154, 154, 154);
}

.nav a {
  text-decoration: none;
  color: black;
}

.settings {
  display: block;
  margin: 30px 0 0 0;
}

.line {
  border-bottom: 2px solid black;
  margin: 30px 30px 0 30px;
  height: 0px;
}

.link a:hover {
  font-weight: 600;
}

.link .active {
  color: red;
  font-weight: 600;
}

@media(min-width: 1150px) {
  .nav{
    font-size: 25px;
  }
}